import React, { useState } from "react";
import thunder from "./images/Thunder storm.png";
import "./App.css";
import { HashRouter as Router, Switch, Route, Link } from "react-router-dom";
import { Helmet } from "react-helmet";
import ReactAnimatedWeather from "react-animated-weather";
import Navbar from "./components/navbar";
import Menu from "./components/menu";

let api = {
  key: "a8d08990f90ee525f0f0c5118f128af9",
  base: "https://api.openweathermap.org/data/2.5/",
};
function App() {
  $(".main").ripples({
    resolution: 720,
    dropRadius: 10,
    perturbance: 0.04,
  });

  const [city, setCity] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [weather, setWeather] = useState(null);
  const [edata, setEdata] = useState(null);
  function search(e) {
    let lat = 0;
    let lon = 0;
    if (e.key === "Enter" || e.target.id === "search-btn") {
      fetch(`${api.base}weather?q=${city}&units=metric&appid=${api.key}`)
        .then((response) => {
          if (!response.ok) {
            setCity("");
            throw Error("Could not get the data, Try again");
          }
          return response.json();
        })
        .then((data) => {
          setWeather(data);
          // console.log(data);
          lat = data.coord.lat;
          lon = data.coord.lon;
          // console.log(lat);
          fetch(
            `${api.base}onecall?lat=${lat}&lon=${lon}&units=metric&exclude=minutely,daily&appid=${api.key}`
          )
            .then((res) => res.json())
            .then((extraData) => {
              setEdata(extraData);
              // console.log(edata);
              setLoading(false);
              setError(null);
            });
          setCity("");
        })
        .catch((err) => {
          setLoading(false);
          setError(err.message);
          setWeather(null);
          setEdata(null);
        });
      setLoading(true);
    }
  }
  function animateIcon(i, size) {
    let icon = "";
    switch (i) {
      case "01d":
        icon = "CLEAR_DAY";
        break;
      case "01n":
        icon = "CLEAR_NIGHT";
        break;
      case "02d":
        icon = "PARTLY_CLOUDY_DAY";
        break;
      case "02n":
        icon = "PARTLY_CLOUDY_NIGHT";
        break;
      case "03d":
      case "03n":
      case "04d":
      case "04n":
        icon = "CLOUDY";
        break;
      case "09d":
      case "09n":
        icon = "SLEET";
        break;
      case "10d":
      case "10n":
        icon = "RAIN";
        break;
      case "13d":
      case "13n":
        icon = "SNOW";
        break;
      case "11d":
      case "11n":
        icon = "WIND";
        break;
      case "50d":
      case "50n":
        icon = "FOG";
        break;
      default:
        size = 0;
    }
    return icon == "WIND" ? (
      <img src={thunder} alt="" width="128" id="thunder-icon" />
    ) : icon == "" ? (
      ""
    ) : (
      <ReactAnimatedWeather
        icon={icon}
        color={"white"}
        size={size}
        animate={true}
      />
    );
  }
  let timezone = weather != null ? weather.timezone : 19800;
  let dt = new Date();
  function displayTime(date) {
    let d = new Date(date * 1000 + timezone * 1000);
    let st = d.toISOString();
    return st.substr(11, 5);
  }
  function displayDate(date) {
    let d = new Date(date * 1000);
    let st = d.toDateString();
    return st.substr(4, 6);
  }
  let percent = 0,
    percent2 = 0;
  if (weather != null) {
    percent =
      ((dt / 1000 - weather.sys.sunrise) /
        (weather.sys.sunset - weather.sys.sunrise)) *
      100;
    if (percent < 0) percent = 0;
    else if (percent > 100) percent = 100;
    percent2 = percent - 3;
    percent = percent.toString();
    percent2 = percent2.toString();
  }
  percent += "%";
  percent2 += "%";
  return (
    <Router>
      <div className="App">
        <div className="main">
          <div className="glass">
            <Navbar />
            <div className="content">
              <div id="logo" className="responsive">
                Weatherly
              </div>
              <div id="form">
                <input
                  type="text"
                  id="search-box"
                  className="search"
                  placeholder="Type Here"
                  onChange={(e) => setCity(e.target.value)}
                  value={city}
                  onKeyDown={search}
                />
                <button className="search" id="search-btn" onClick={search}>
                  Search
                </button>
              </div>
              {error && <div>{error}</div>}
              {weather != null ? (
                edata != null ? (
                  <div className="base">
                    <div id="city">
                      {!loading ? (
                        <div>
                          {weather.name} {weather.sys.country}
                        </div>
                      ) : (
                        "Loading..."
                      )}
                    </div>
                    <Switch>
                      <Route exact path="/">
                        <Helmet>
                          <title>Weatherly | Home</title>
                        </Helmet>
                        <div className="changing-content home">
                          <div className="upper">
                            <div id="temp">
                              {!loading ? (
                                <div>{Math.round(weather.main.temp)}°C</div>
                              ) : (
                                "..."
                              )}
                            </div>
                            <div id="date">
                              {!loading ? (
                                <div>
                                  {new Date().toString().substr(0, 15)},{" "}
                                  {new Date().toTimeString().substr(0, 5)}
                                </div>
                              ) : (
                                "..."
                              )}
                            </div>
                          </div>
                          <div className="description">
                            <div id="desc">
                              {!loading ? (
                                <div>{weather.weather[0].description}</div>
                              ) : (
                                "..."
                              )}
                            </div>
                            <div>
                              {!loading ? (
                                <div>
                                  {animateIcon(weather.weather[0].icon, 128)}
                                </div>
                              ) : (
                                ""
                              )}
                            </div>
                          </div>
                        </div>
                      </Route>
                      <Route path="/forecast">
                        <Helmet>
                          <title>Weatherly | Forecast</title>
                        </Helmet>
                        <div className="changing-content about">
                          {edata.hourly.map((data) => (
                            <div className="block">
                              <div className="forecastDate">
                                {!loading ? (
                                  <div>
                                    {displayDate(data.dt)}
                                    <br />
                                    {displayTime(data.dt)}
                                  </div>
                                ) : (
                                  "..."
                                )}
                              </div>
                              <div>
                                {!loading ? (
                                  <div>
                                    {animateIcon(data.weather[0].icon, 48)}
                                  </div>
                                ) : (
                                  ""
                                )}
                              </div>
                              <div className="forecastDesc">
                                {!loading ? (
                                  <div>{data.weather[0].description}</div>
                                ) : (
                                  ""
                                )}
                              </div>
                            </div>
                          ))}
                        </div>
                      </Route>
                      <Route path="/more">
                        <Helmet>
                          <title>Weatherly | More</title>
                        </Helmet>
                        <div className="changing-content more">
                          <div id="date">
                            {new Date().toString().substr(0, 15)},{" "}
                            {new Date().toTimeString().substr(0, 5)}
                          </div>
                          <div className="box">
                            <div className="tracker">
                              <div className="level">
                                <div
                                  className="completed"
                                  style={{ width: percent }}
                                ></div>
                                <div
                                  className="sun"
                                  style={{ left: percent2 }}
                                ></div>
                              </div>
                              <div className="time">
                                <div className="times">
                                  <p>Sunrise </p>
                                  <div id="sunrise">
                                    {!loading ? (
                                      <div>
                                        {displayTime(weather.sys.sunrise)}
                                      </div>
                                    ) : (
                                      "..."
                                    )}
                                  </div>
                                </div>
                                <div className="times">
                                  <p>Sunset </p>
                                  <div id="sunset">
                                    {!loading ? (
                                      <div>
                                        {displayTime(weather.sys.sunset)}
                                      </div>
                                    ) : (
                                      "..."
                                    )}
                                  </div>
                                </div>
                              </div>
                            </div>
                            <div className="moreData">
                              <div className="col">
                                <div className="aqi">
                                  <p>Visibility</p>
                                  <div id="aqiData">
                                    {!loading ? (
                                      <div>
                                        {Math.round(weather.visibility * 100) /
                                          100000}
                                        km
                                      </div>
                                    ) : (
                                      "..."
                                    )}
                                  </div>
                                </div>
                                <div className="aqi">
                                  <p>wind speed</p>
                                  <div id="windData">
                                    {!loading ? (
                                      <div>
                                        {edata.current.wind_speed}m/s{" "}
                                        <span>{edata.current.wind_deg}° N</span>
                                      </div>
                                    ) : (
                                      "..."
                                    )}
                                  </div>
                                </div>

                                <div className="aqi">
                                  <p>Humidity</p>
                                  <div id="humidData">
                                    {!loading ? (
                                      <div>{weather.main.humidity}%</div>
                                    ) : (
                                      "..."
                                    )}
                                  </div>
                                </div>
                                <div className="aqi">
                                  <p>UV Index</p>
                                  <div id="uvData">
                                    {!loading ? (
                                      <div>{edata.current.uvi}</div>
                                    ) : (
                                      "..."
                                    )}
                                  </div>
                                </div>

                                <div className="aqi">
                                  <p>Feels-like Temp</p>
                                  <div id="corData">
                                    {!loading ? (
                                      <div>{weather.main.feels_like}°C</div>
                                    ) : (
                                      "..."
                                    )}
                                  </div>
                                </div>
                                <div className="aqi">
                                  <p>Pressure</p>
                                  <div id="presData">
                                    {!loading ? (
                                      <div>{weather.main.pressure}hPa</div>
                                    ) : (
                                      "..."
                                    )}
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      </Route>
                    </Switch>
                  </div>
                ) : (
                  ""
                )
              ) : (
                ""
              )}
            </div>
          </div>
          <Menu />
        </div>
      </div>
    </Router>
  );
}

export default App;
