import React, { Component } from 'react'
import { HashRouter as NavLink, Link } from "react-router-dom";
import homeImg from "../images/home-icon.png";
import forecastImg from "../images/forcast-icon.png";
import moreImg from "../images/more-icon.png";
import "./navbar.css"

export default class menu extends Component {
    render() {
        return (
            <div className="nav-below">
                <ul id="navigation">
                    <Link to="/">
                        <li>
                            <a>
                                <img src={homeImg} alt="" />
                            </a>
                        </li>
                    </Link>
                    <Link to="/forecast">
                        <li>
                            <a>
                                <img src={forecastImg} alt="" />
                            </a>
                        </li>
                    </Link>
                    <Link to="/more">
                        <li>
                            <a>
                                <img src={moreImg} alt="" />
                            </a>
                        </li>
                    </Link>
                </ul>
            </div>
        )
    }
}
