import "./navbar.css";
import { HashRouter as NavLink, Link } from "react-router-dom";
import homeImg from "../images/home-icon.png";
import forecastImg from "../images/forcast-icon.png";
import moreImg from "../images/more-icon.png";
import React, { Component } from "react";

export default class navbar extends Component {
  render() {
    return (
      <>
        <nav>
          <Link to="/">
            <a id="logo">Weatherly</a>
          </Link>
          <ul id="navigation">
            <Link to="/">
              <li>
                <a>
                  <img src={homeImg} alt="" />
                  Home
                </a>
              </li>
            </Link>
            <Link to="/forecast">
              <li>
                <a>
                  <img src={forecastImg} alt="" />
                  Forecast
                </a>
              </li>
            </Link>
            <Link to="/more">
              <li>
                <a>
                  <img src={moreImg} alt="" />
                  More
                </a>
              </li>
            </Link>
          </ul>
          <a
            href="https://piyushchittora.github.io/My-Portfolio/"
            target="_blank"
          >
            <button id="connect">Let's Connect</button>
          </a>
        </nav>
      </>
    );
  }
}
